<template>
    <div class="sworks">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-calendar"></i> 作业项目
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="form"> 
                <div class="form-top">
                    <el-button class="fr" type="primary" @click="openLink(0)" size="small">新建作业项目</el-button>
                </div>
                <div class="cont"  v-loading="loading">
                    <div class="cont-one" v-for="item in tableData">
                        <div class="one-left">
                            <h2>{{item.name}}</h2>
                            <p class="stu-num"><span><strong>创建者：</strong>{{item.creatorName}}</span> <span style="margin-left:20px;"><strong>创建时间：</strong>{{item.createtime}}</span></p>
                            <p class="work-time"><strong>简介：</strong>{{item.desc}}</p>
                            <p class="data"><strong>资料：</strong><span style="margin-right:20px;" v-for="item1 in item.fileName">{{item1}}</span></p>
                            <p class="data"><strong>参考答案：</strong><span style="margin-right:20px;" v-for="item1 in item.referenceSolutionName">{{item1}}</span></p>
                            <p class="status"><strong>使用：</strong><span>{{item.useCount}}次</span></p>
                        </div>
                        <div class="one-right">
                            <p><el-button type="danger" size="mini" @click="delworks(item.id)" plain>删除作业</el-button></p>
                            <p><el-button type="primary" size="mini" @click="openLink(item.id)" plain>编辑作业</el-button></p>
                        </div>
                    </div>

                    <div class="pagination">
                          <el-pagination
                            background
                            :page-size="pageSize"
                            @current-change="changePage"
                            layout="prev, pager, next"
                            :total="total">
                          </el-pagination>
                      </div>
                    
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {fetchGetworkprojectlist, fetchDeleteworkproject} from '../../axios/requests'
export default {
    name: 'course',
    data() {
        return {
            loading:false,
            pageNum:1,
            pageSize:10,
            total:0,

            tableData:[]
        };
    },
    mounted(){
        this.Getworkprojectlist()
    },
    methods: {
        openLink(type){
            this.$openBlank('newworks',{type});
        },
        delworks(id){
            this.$confirm('确定要删除吗？', '提示', {
                type: 'warning'
            })
            .then(() => {
                this.Deleteworkproject(id)
            })
            .catch(() => {});
        },
        async Deleteworkproject(id) {//删除作业项目
          const loading = this.$loading({
            lock: true,
            text: '加载中...',
            spinner: 'el-icon-loading'
          });
          try {
            let response = await fetchDeleteworkproject(id);
            if (response.status.code ==0) {
              this.$message({
                message: '删除成功',
                type: 'success'
              });
              this.Getworkprojectlist();
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            loading.close()
          }
      },
        changePage(page){
          this.pageNum = page;
          this.Getworkprojectlist();
        },
        async Getworkprojectlist() {//得到列表
          this.loading = true;
          let {pageNum, pageSize} = this;
          try {
            let response = await fetchGetworkprojectlist(pageNum, pageSize);
            if (response.status.code ==0) {
              this.total = response.result.total;
              this.tableData = response.result.list;
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            this.loading = false;
          }
        },
    }
};
</script>
<style lang='scss' scoped>
    .sworks{
        .form-top{
            overflow:hidden;
        }
    }
    .cont-one{
        display:flex;
        padding:15px 20px;
        border:1px solid #ccc;
        margin-top:20px;
        .one-left{
            flex:1;
            font-size:14px;
            min-width:0;
            line-height:24px;
            color:#666;
            strong{
                font-weight:500;
                color:#333;
            }
            h2{
                font-size:16px;
                line-height:28px;
            }
            .work-time,.data{
                overflow: hidden;
                width:100%;
                text-overflow:ellipsis;
                white-space: nowrap;
            }
        }
        .one-right{
            width:90px;
            padding-top:20px;
            p{
                margin-top:10px;
            }
            button{
                text-align: right;
                display:block;
                margin-left:10px;
            }
        }
    }
    .pagination{
        margin-top:20px;
    }
</style>


